.possibility-container{
    display: flex;

}
.poss-image{
    flex:1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.poss-image img{
    width: 100%;
    height: 100%;
}
.poss-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.poss-content h4{
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71e5ff;

}
.poss-content h4:last-child{
    color: #ff8a71;

}
.poss-content h1{
    margin: 1rem 0;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.poss-content p{
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 2rem;
    color: ghostwhite;
}

@media  screen and (max-width: 950px) {
    .possibility-container{
        flex-direction: column;
    }
    .poss-image{
        margin: 1rem 0;
    }
    .poss-content {
        margin-top: 2rem;
    }
}