.features-container{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin: 1rem;
    align-items: flex-start;
}

.features-title{
    flex:1;
    margin-right: 2rem;
    max-width: 150px;
}
.features-title h1{
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    color: #fff;
}
.features-title div{
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow:  0 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.text-content{
 max-width: 390px;
 flex: 2;
 display: flex;   
}
.text-content p{
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

@media screen and(max-width:550px) {
    .features-title h1{
            font-size: 14px;
            line-height: 22px;
    }
    .features-container{
        margin: 1rem 0;
    }
}

