.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.nav-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.logo{
    margin-right: 2rem;
}
.logo > img{
    width: 62px;
    height: 16px;
}

.nav-list{
    display: flex;
}
.nav-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.nav-list p,
.nav-sign p,
.nav-container p{
    color: #fff;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    cursor: pointer;
    margin: 0 1rem;
}


.nav-sign button,
.nav-container button {
    cursor: pointer;
    font-weight: 500;
    padding: 0.5rem 1rem;
    background: #ff4820;
    border-radius: 5px;
    color: #fff;
    border: none;
    font-size: 18px;
    line-height: 25px;
    outline: none;
}

.nav-menu{
    margin-left: 1rem;
    display: none;
    position: relative;
}
.nav-menu svg{
    cursor: pointer;
}
.nav-container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: start;
    background: var(--color-footer);
    padding:  2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0 , 0, 0, 0.2);
}

.nav-menu p{
    margin: 1rem 0; 
}
.nav-container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px){
    .nav-list{
        display: none;
    }
    .nav-menu{
        display: flex;
    }
}

@media screen and (max-width: 550px){
    .navbar{
        padding: 2rem 4rem;
    }
    .nav-sign{
        display: none;
    }
    .nav-container{
        top: 20px;
    }
    .nav-container-links-sign{
        display: block;
    }
}