.cta{
    display: flex;
    border: 1px solid white;
    color: #fff;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);
}
.cta-content p{
    font-weight:500 ;
    font-size: 12px;
    line-height: 30px;
    color: #0e0e0e;
}
.cta-content h3{
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000;
}
.cta-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 2rem;
}
.cta-btn button{
    background: #000;
    border-radius: 40px;
    color:white;
    font-size: 18px;
    line-height: 30px;
    font-weight: bold;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    min-width: 150px;
    cursor: pointer;
}

@media screen and (max-width:650px) {
    .cta{
        flex-direction: column;

    }
    .cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .cta{
        margin: 4rem 2rem;
    }
    .cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .cta-btn button{
        font-size: 14px;
        line-height: 28px;
    }
}