.article{
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    background: var(--color-footer);

}

.img{
    width: 100%;
    height: 100%;
    background-color: var(--color-footer);
}

.img img{
    width: 100%;

}
.text-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.text-content p{
    font-size: 12px;
    font-weight: bold;
    line-height: 35px;
    color: #fff;
}
.text-content h3{
    font-weight: 800;
    font-size: 25px;
    line-height: 30px;
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}
.text-content p:last-child{
    cursor: pointer;
}

@media screen and (max-width: 550px){
    .text-content h3{
        font-size: 18px;
        line-height: 25px;
    }
}