.blog{
    display: flex;
    flex-direction: column;
}
.blog-header{
    width: 100%;
    text-align: left;
    margin-bottom: 5rem;
}

.blog-header h1{
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
}

.blog-container{
    display: flex;
    flex-direction: row;
}
.grpA{
    flex: 0.75;
    margin-right: 2rem;
}
.grpB{
    flex: 1;
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr 1fr;
}

@media screen and (max-width: 992px){
    .blog-container{
        flex-direction: column-reverse;
    }
    .grpA{
        margin: 2rem 0;
    }
}

@media screen and (max-width: 700px){
    .grpB{
        grid-template-columns: 1fr;
    }
    .blog-header h1{
        font-size: 46px;
        line-height: 52px;
    }
}
@media screen and (max-width: 550px){
   .blog-header h1{
        font-size: 34px;
        line-height: 42px;
   }
}
