.header {
  display: flex;
}

.header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}
.header-content h1 {
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04rem;
  color: var(--color-text);
}

.header-content p {
  font-size: 20px;
  line-height: 27px;
  color: var(--color-text);
  margin-top: 1.5rem;
}
.input-form {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}
.input-form input {
  background: #052d56;
  border-radius: 5px 0 0 5px;
  font-size: 20px;
  line-height: 27px;
  border: 2px solid var(--color-footer);
  color: #3d6184;
  padding: 0 1rem;
  outline: none;
  background: var(--color-footer);
  flex: 2;
  width: 100%;
  min-height: 50px;
}
.input-form button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  font-weight: bold;
  background: #ff4820;
  border-radius: 0 5px 5px 0;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  border: 0;
  cursor: pointer;
  padding: 0 1rem;
  outline: none;
}

.people {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-top: 2rem;
}
.people img {
  width: 182px;
  height: 38px;
}
.people p {
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
  margin: 0;
  margin-left: 1rem;
  text-align: center;
}
.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
}
.hero img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px) {
  .header {
    flex-direction: column;
  }
  .header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .header h1 {
    font-size: 48px;
    line-height: 60px;
  }
  .header p,
  .input-form input,
  .input-form button{
    font-size: 16px;
    line-height: 24px;
  }
  .people {
    flex-direction: column;
  }
  .people p {
    margin: 0;
  }
}


@media screen and (max-width: 490px) {
    .header h1 {
      font-size: 36px;
      line-height: 48px;
    }
    .header p{
      font-size: 14px;
      line-height: 24px;
    }
    .input-form input,
    .input-form button{
      font-size: 12px;
      line-height: 16px;
    }
  }
  