.footer{
    color: #fff;    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: var(--color-footer);
}

.footer-header{
    width: 100%;
    margin-bottom: 3em;
    text-align: center;
}

.footer-header h1{
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    text-align: center;

}
.footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    cursor: pointer;
    margin-bottom: 10rem;
}
.footer-btn p{
    box-sizing: border-box;
    font-size: 18px;
    color: #fff;
    word-spacing: 2px;
}

.footer-links{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: nowrap;
    width: 100%;
    text-align: left;
}

.footer-links div{
    width: 250px;
    margin: 1rem;
}

.links-logo{
    display: flex;
    flex-direction: column;
}

.links-logo img{
    width: 118px;
    height: 30px;
    margin-bottom: 3rem;
}
.links-logo p{
    font-size: 12px;
    line-height: 14px;
}

.link-items{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.link-items h4{
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 1rem;
}
.link-items p{
    font-size: 12px;
    line-height: 14px;
    cursor: pointer;
    margin: 0.5rem 0;
}

.footer-copyight{
    text-align: center;
    width: 100%;
    margin-top: 2rem;
}
.footer-copyight p{
    font-size: 12px;
    line-height: 14px;
    color: #fff;
}

@media screen and (max-width:992px){
    .footer-links{
        flex-wrap: wrap;
    }
}

@media screen and (max-width: 850px){
    .footer-header h4{
        font-size: 44px;
        line-height: 50px;
    }
    
}


@media screen and (max-width: 550px){
    .footer-header h4{
        font-size: 34px;
        line-height: 42px;
    }
    .footer-links div{
        margin: 1rem 0;
    }
    .footer-btn p{
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px){
    .footer-header h1{
        font-size: 27px;
        line-height: 38px;
    }
}