.gpt {
  display: flex;
  flex-direction: column;
  padding: 2rem;

  /* global 92%+ browsers support */
  background: radial-gradient(
    circle at 30% -100%,
    #042c54 25%,
    rgba(4, 44, 84, 1) 85%,
    rgba(27, 120, 222, 1) 100%
  );

  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt-feature {
  display: flex;
}
.gpt-feature .features-container {
  margin: 0;
}

/* Customizing feature component as per needs */
.gpt-feature .text-content {
  max-width: 700px;
}

.gpt-heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 4rem 0 2rem;
}

.gpt-heading > h1 {
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  max-width: 510px;
}

.gpt-heading > p {
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-subtext);
  cursor: pointer;
}
.feature-container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 2rem;
}


.feature-container .features-container {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.feature-container .features-title {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gpt-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt-feature .features-container {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt-feature .text-content {
        margin-top: 0.5rem;
    }

    .gpt-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}
